#common-home {
  #footer {
    background: $light-color;
    margin-top: 0;
  }
}
/* footer */

#footer {
  margin-top: 30px;
  background: #f7f7f7;
  color: $brand-primary;
  clear: both;
  line-height: 1.25em;
  hr {
    border-top: none;
    border-bottom: 1px solid #666;
  }
  section {
    margin: 0;
  }
  &.home-footer {
    margin-top: 0;
  }
  .ishistoreinfoblock {
    .ishistoreinfo-inner {
      .store-logo {
        &:hover {
          padding-left: 0;
        }
      }
    }
  }
  #slidetop{
    background: $brand-secondary none repeat scroll 0 0;
    box-shadow: 0 0 10px 4px rgba(255, 255, 255, 0.1);
    color: transparent;
    border-radius: 50%;
    bottom: 20px;
    cursor: pointer;
    height: 40px;
    overflow: hidden;
    padding: 6px;
    position: fixed;
    right: 20px;
    text-align: center;
    width: 40px;
    z-index: 99;
    display: none;
    -webkit-transition: all 0.5s;
    -moz-transition: all 0.5s;
    -o-transition: all 0.5s;
    transition: all 0.5s;
    &:before{
      content: "\f106";
      display: inline-block;
      color:$brand-primary;
      font-family: FontAwesome;
      font-size: 26px;
      left: 0;
      position: absolute;
      right: 0;
      top: 10px;
    }
    &:after {
      display: none;
    }
    &:hover{
      background-color: $brand-primary;
      &:before {
        color:$light-color;
      }
    }
  }
  .footer-aftertext {
    a {
      color: $brand-secondary;
      line-height: 22px;
    }
  }
  a {
    font-family: $primary-font;
    font-weight: 400;
    letter-spacing: 0.5px;
    font-size: 14px;
    color: $black-color;
    text-decoration: none;
    position: relative;
    overflow: hidden;
    -webkit-transition: all 0.2s;
    -moz-transition: all 0.2s;
    -o-transition: all 0.2s;
    transition: all 0.2s;
    &:hover {
      color: $brand-secondary;
    }
  }
  h5, .h3 {
    margin: 0 0 20px 0;
    color: #000;
    text-transform: uppercase;
    font-size: 16px;
    font-weight: 500;
    cursor: pointer;
    font-family: $secondary-font;
  }
  li {
    margin-bottom: 15px;
  }
  .block-social { 
    text-align: center;
    display: block;
    overflow: hidden;
    padding: 5px;
    @media (max-width: 991px) {/* If media is below 992 */
      margin: 0 0 8px;
    }
    ul{
      padding: 0;
      margin: 0;
      text-decoration: none;
      li{
        @media (max-width: 350px) {/* If media is below 350 */
          margin: 2px 1px;
        }
        cursor: pointer;
        display: inline-block;
        vertical-align: middle;
        border-radius: 50%;
        height: 30px;
        width: 30px;
        padding: 3px;
        margin: 1px 1px;
        text-align: center;
        position: relative;
        -webkit-transition: all 0.4s;
        -moz-transition: all 0.4s;
        -o-transition: all 0.4s;
        transition: all 0.4s;
        a, i {
          text-decoration: none;
          &:hover{
            padding:0 ! important;
            color: transparent;
          }
          &:before {
            display: inline-block;
            vertical-align: middle;
            font-family: FontAwesome;
            font-weight: 400;
            position: relative;
            font-size: 16px;
            line-height: 23px;
            color: $light-color;
          }
        }    
        &.facebook{
          background: #3b5999;
        }
        &.twitter{
          background: #00aced;
        }
        &.youtube{
          background: #d12121; 
        } 
        &.googleplus{
          background: #dc473b;
        } 
        &.rss{
          background: #f26522;
        }
        &.pinterest{
          background: #cb2027;
        }
        &.vimeo{
          background: #00aced;
        }
        &.instagram{
          background: #f09433;
        }
        &:hover{
          background: $brand-secondary;
          a, i {
            &:before {
              color: $brand-primary;
            }
          }
        }
      }
    }
  }
  .footer-before {
    display: block;
    overflow: hidden;
    position: relative;
    padding: 30px 0;
    border-top: 1px solid $border-color;
    border-bottom: 1px solid $border-color;
  }
  .footer-container {
    padding: 50px 0 40px;
    display: block;
    overflow: hidden;
    position: relative;
    .services {
      display: none;
    }
    .block-contact {
      display: block;
      overflow: hidden;
      .block{
        margin-bottom: 15px;
        letter-spacing: 0.5px;
        font-size: 14px;
        font-weight: 400;
        .icon{
          content: "";
          color: #666;
          font-family: FontAwesome;
          font-size: 18px;
          height: 30px;
          width: 30px;   
          position: relative;
          top: -1px;   
          line-height: 29px;
          vertical-align: middle;
          display: inline-block;
          text-align: center;
        } 
        .content{
          line-height: 25px;
          overflow: hidden; 
          display: inline-block;
          vertical-align: middle;
          padding-left: 5px;   
          color: #666;  
          width: 80%;
          a{
            &:after {
              display: none;
            }
            &:hover {
              padding-left: 0;
            }
          }
        }
      }
    }
  }
  .footer-dropdown {
      margin-top:10px;
  }
  @media (max-width: 992px) {/* If media is above 991 */
    .footer-block {
      margin-bottom: 15px;
      &:last-child {
        margin-bottom: 0;
      }
    }
  }
  @media (min-width: 1200px) {/* If media is above 1199 */
    .collapse {
        display: inherit;
    }
    .footer-dropdown {
      margin-top:0;
    }
  }
  @media (max-width: 543px) {/* If media is below 544 */
    .footer-block {
      margin-bottom:15px;
    }
  }
  @media (min-width: 992px) {/* If media is above 991 */
    .collapse {
        display: inherit;
    }
  }
  .footer-title {
    .navbar-toggler {
      top: 0;
      right: 0;
      color: $brand-primary;
    }
  }
}
.footer-after {
  background: $brand-primary;
  color: $light-color;
  .footer-aftertext {
    text-align: left;
    color: $light-color;
    @media (max-width: 992px) {/* If media is above 991 */
      text-align: center;   
    }
    margin-bottom:0;
    padding:9px 0;
    a{
      color: $light-color;
      &:after {
        display: none;
      }
    }
  }  
  >.container {
    @media (max-width: 544px) {/* If media is below 544 */
      padding: 15px 15px 50px;
    }
    padding-top: 4px;
    padding-bottom: 4px;
  }
}


