.ishispecial {
	text-align: center;
	.owl-nav {
		@media (min-width: 544px) {/* If media is above 543 */
			opacity: 0;
		}
	}
	&:hover {
	    .owl-nav {
	     	@media (min-width: 544px) {/* If media is above 543 */
	          	opacity: 1;
	      	}
	    }
	}
}


