#product-compare {
	.btn-danger, .btn-primary {
		margin: 2px 0;
	}
	@media (max-width: 991px) {/* If media is below 992 */
		.table-bordered {
		    float: left;
		    width: 100%;
		    overflow: auto;
		    display: inline;
		}
	}
}
.product-left {
	.image {
		margin-bottom: 20px;
	}
	.thumbnail {
		border: 1px solid #eee;
		border-radius: 0;
		margin-bottom: 0;
		padding: 0;
	    img {
	      width: 100%;
	    }
	    &:hover, &:focus {
	    	border-color: #eee;
	    }
	}
	.image-carousel {
		margin: 0 20px 30px;
		position: relative;
		.owl-nav {
			top: 35%;
			.owl-prev, .owl-next {
				left: -15px;
				right: auto;
				position: absolute;
				top: 50%;
				margin-top: -12px;
				border: 0;
				background-color: $brand-secondary;
				border-radius: 50%;
				padding: 5px;
				width: 25px;
				height: 25px;
				overflow: hidden;
				-webkit-transition: all 0.3s ease-in-out;
				-moz-transition: all 0.3s ease-in-out;
				-o-transition: all 0.3s ease-in-out;
				transition: all 0.3s ease-in-out;
			}
			.owl-next {
				right: -15px;
				left: auto;
			}
		}
	}	
}
.product-right {
	.product-title {
		font-size: 24px;
		font-weight: 500;
		margin: 0 0 18px;
		color: #333;
		line-height: 30px;
	}
	.rating-wrapper {
	    margin: 10px 0 20px;
	    .fa-star, .fa-star-o {
	        color: $brand-secondary;
	        font-size: 13px;
	        line-height: 6px;
	    }
	    .fa-star-o {
	       	color: #999
	    }
	    span{
		    &.fa-stack {              
		        width: 8px;
		        height: 8px;
		        margin-right: 3px;
		    }
	    }
	    .review-count, .write-review {
	    	margin: 0 10px;
	    }
	}
	.list-unstyled {
		margin-bottom: 0;		
		&.attr {
			padding: 10px 0;
			border-top: 1px solid #e0e0e0;
			border-bottom: 1px solid #e0e0e0;
			margin-bottom: 20px;			
			li {
			    line-height: 26px;
			    padding: 8px 0;
			    span {
					font-weight: 600;
					color: #333;
					font-size: 14px;
				}
			}
		}
		&.price {
			margin-bottom: 15px;
			font-size: 13px;
			line-height: 20px;
			.product-price {
				margin: 0 10px 15px;
				font-size: 18px;
				font-weight: 600;
				display: inline-block;
				vertical-align: middle;
				h2 {
					margin: 0;
					font-size: 24px;
				}
			}
			.product-dis {
				display: inline-block;
				vertical-align: middle;
				font-size: 20px;
				color: #999;
				margin-bottom: 15px;
			}
			.product-tax {
				margin-bottom: 12px;
			}
		}
	}
	#product {
	    .form-group {
	    	margin-bottom: 20px;
	    	overflow: hidden;
			position: relative;
			clear: both;
		    .control-label {
			    float: left;
			    margin: 2px 10px 3px 0;
			    font-size: 14px;
			    &.radio-text, &.checkbox-text {
			    	float: none;
			    	margin: 0;
			    }
			}
		    #input-quantity {
			    margin-right: 10px;
			    margin-bottom: 0;
			    width: auto;
			    float: left;
				height: 37px;
			    text-align: center;
			}
			#button-cart {
				display: inline-block;
				vertical-align: middle;
				width: auto;				
			}
			.wishlist, .compare {
				@media (max-width: 378px) {/* If media is below 377 */
					margin: 5px 0;
				}
				padding: 0;
				background: $brand-secondary url(../image/product-sprite.png) no-repeat;
				display: inline-block;				
				vertical-align: middle;
				height: 36px;
				width: 36px;	
				border: 0;
				text-align: center;
				text-transform: capitalize;
				-webkit-transition: all 0.0s;
				-moz-transition: all 0.0s;
				-o-transition: all 0.0s;
				transition: all 0.0s;
				i {
					display: none;
				}
				&.wishlist {
					background-position: 7px 5px;
				}
				&.compare {
					background-position: 7px -87px;
				}
				&:hover {
					background-color: $brand-primary;
					&.wishlist {
						background-position: -78px 5px;
					}
					&.compare {
						background-position: -78px -87px;
					}
				}
			}
			.input-group {
				&.date, &.time, &.datetime {
					display: inline-table;
					.btn-default {
						padding: 6px 18px;
					}
				}				
			}
			.btn-file {
				display: inline-block;
				vertical-align: middle;
				width: auto;
			}			
		}
	}
	.addthis_toolbox {
		margin-bottom: 10px;
	}
}
.tabs_info {
	@media (max-width: 991px) {/* If media is below 992 */
    	margin-bottom:30px;
  	}
  	margin-bottom: 30px;
  	text-align: center;
	.nav-tabs {
	    border-bottom: none;
	    margin-bottom: 30px;
	    text-align: center;
	    a{
	    	@media (max-width: 767px) {/* If media is below 768 */
	    		padding: 10px 20px;
	    	}
	    	@media (max-width: 543px) {/* If media is below 544 */
		    	padding: 6px 10px;
				font-size: 11px;
				margin: 3px 0;
			}
		    color: #000;
			display: block;
			text-transform: uppercase;
			float: left;
			font-weight: 600;
			font-size: 13px;
			line-height: 21px;
			margin: 0px;
			padding: 10px 40px;
			text-align: center;
			position: relative;
			border: 1px solid #ddd;
			border-radius: 0;
		}
	    li {
			list-style: none outside none;
			position: relative;
			text-align: center;
			margin: 0 6px;
			float: none;
	    	display: inline-block;
			vertical-align: middle;
	    	&.active {
	    		a{
			        border-color:$brand-secondary;
			        background: $brand-secondary;
			        color: $brand-primary;
			    }
	      	}
	      	a{
	      		&:hover {
			        border-color:$brand-secondary;
			        background: $brand-secondary;
			        color: $brand-primary;
	      		}
	      	}
	    }    
	}
	.tab-content {
	    padding: 25px;
	    border: 1px solid #e4e4e4;
	    text-align: left;
	    .tab-pane {
	       	p{
		        line-height: 28px;
		        color: gray;
		        margin-bottom: 0;
		        letter-spacing: 0.8px;
	       	}
	    }
	    #tab-review {
	    	.table-bordered {
	    		border: 1px solid #e5e5e5;
	    		> tbody {
	    			> tr {
	    				> td {
	    					strong {
	    						font-weight: 600;
	    					}
	    					span{
							    &.fa-stack {              
							        width: 8px;
							        height: 8px;
							        margin-right: 3px;
							    }
						    }
	    					.fa-star, .fa-star-o {
						        color: $brand-secondary;
						        font-size: 13px;
						        line-height: 6px;
						    }						    
	    				}
	    			}
	    		}
	    	}
	    	h2 {
		    	margin: 8px 0;
				font-size: 18px;
				font-weight: 500;
				color: #000;
			}
			.form-horizontal {
				.form-group {
				  	.control-label {
				      	padding: 7px 0 5px;
				  	}
				}
			}
	    }
	}	
}
#product-product {
	.related-product {
		text-align: center;
		margin-bottom: 30px;
		.owl-nav {
			@media (min-width: 544px) {/* If media is above 543 */
				opacity: 0;
			}
		}
		&:hover {
		    .owl-nav {
		     	@media (min-width: 544px) {/* If media is above 543 */
		          	opacity: 1;
		      	}
		    }
		}
	}
}


