/* menu */
#_desktop_top_menu {
    text-align: center;
    margin: 0 auto;
    display: block;
   #top-menu {
     margin: 0;
   }
}
#top-menu {  
  > li {
    display: inline-block;
    vertical-align: middle;
    position: relative;
    text-transform: uppercase;    
    cursor: pointer;
    @media (max-width: 991px) {/* If media is below 992 */
      display: block;
      padding: 2px 0;
      overflow: hidden;
    }
    &:last-child {
      > a{
        &:before {
          display: none;
        }
      }
    }
    .dropdown-item {
      white-space: normal;      
    }
    > a {
      @media (min-width: 992px) {/* If media is above 991 */
        color: $light-color;
      }
      padding: 15px 24px;
      font-size: 16px;
      display: block;
      line-height: 18px;
      font-weight: 400;
      text-transform: capitalize;
      position: relative;
      font-family: $secondary-font;
      &:before {
        @media (min-width: 992px) {/* If media is above 991 */
          content: '';
          position: absolute;
          right: 0;
          top: 12px;
          width: 1px;
          height: 25px;
          background: #333333;
        }
      }     
    }
    #_desktop_top_menu & {
      .sub-menu {
        &.collapse {
          opacity: 0;
          -webkit-transition: opacity .4s,top .4s,bottom .4s,visibility .4s;
          -o-transition: opacity .4s,top .4s,bottom .4s,visibility .4s;
          transition: opacity .4s,top .4s,bottom .4s,visibility .4s;
          &:before {
            content: '';
            position: absolute;
            bottom: -1px;
            left: 0;
            width: 0px;
            height: 2px;
            background: $brand-secondary;
            -webkit-transition: all 0.3s;
            -moz-transition: all 0.3s;
            -o-transition: all 0.3s;
            transition: all 0.3s;
          }
        }        
        top: 130%;
        left: 0;
        visibility: hidden;
        border: none;
        width: auto;
        z-index: 18;
        padding: 15px 10px;
        border: 1px solid #ddd;
        border-radius: 0;
        margin: 0;
        -webkit-transition: all 0.3s;
        -moz-transition: all 0.3s;
        -o-transition: all 0.3s;
        transition: all 0.3s;
        text-align: center;
        ul[data-depth="1"] > li {
          float: left;
          width: 200px;
          text-align: left;
          font-weight: 400;
        }
        ul[data-depth="2"] {
          > li {
            line-height: 26px;
            a {
                text-transform: capitalize;
                padding: 3px 20px;
                clear: both;
                font-weight: 400;
            }
          }
        }
        a {
          padding: 3px 20px;
          font-weight: 600;
          color: $brand-primary;
          text-transform: uppercase;
          &:hover {
            color: $brand-secondary;
          }
        }

      }
      &:nth-child(8) .sub-menu{
        right: 0;
        left: auto;
      }
    }
    &:hover {
      #_desktop_top_menu & { 
        &:hover {
           .sub-menu {
              top: 100%;
              opacity: 1;
              visibility: visible;
              &.collapse {
                &:before {
                  width: 100%;
                }
              }
           }
        }
      }
    }
  }   
}
.top-menu {
  a.dropdown-submenu {
    color: $brand-primary;
    display: block;
    width: 100%;
    clear: both;
    background: none;
    border: 0;    
  }
  .collapse {
    display: inherit;
  }
  .category-image {
    max-width: 100%;
    margin-top: 10px;
  }
  .popover {
    max-width: inherit;
  }
}
.popover.bs-tether-element-attached-top {
  margin-top: 0;
}

#canvas {
  background-color: rgba(0,0,0,0.7);
  position: fixed;
  left: 0;
  right: 0;
  bottom: 0;
  top: 0;
}

#mobile_top_menu_wrapper {
  position: fixed;
  top: 0;
  left: 0;
  width: 300px;
  bottom: 0;
  background: white;
  z-index: 999;
  overflow-y:auto;
  overflow-x: auto;
  #top_menu_closer {
    cursor: pointer;
    color:#000;
    margin-top: 8px;
    margin-left: 8px;
    text-align: left;
  }
  #_mobile_top_menu {
    padding:10px;
  }
  #top-menu {
    margin-bottom: 0;
    text-align:left;
    display: block;
    padding: 2px 0;
    background: none;
    > li {
      a{
         padding: 0;
         display: inline-block;
         width: auto;
        margin-bottom: 8px;
      }
      i{
        float: right;
        line-height: 16px;
        font-size: 22px;
      }
    }
  }
 
 
  .top-menu {
    color: $brand-primary;
    .category-image {
      display: none;
    }
    .collapse-icons[aria-expanded="true"] {
      .add {
        display: none;
      }
      .remove {
        display: block;
      }
    }
    .collapse-icons .remove {
      display: none;
    }
    .navbar-toggler {
      display: inline-block;
      padding: 0;
      position: absolute;
      right: 0;
    }
    .collapse {
      display: none;
      &.in {
        display: block;
      }
    }
    .sub-menu {
      &.collapse {
        display: none;
        &.in {
          display: block;
        }
      }
      box-shadow: none;
      z-index: inherit;
      display: block;
      position: static;
      overflow: hidden;
      margin-left: 0;
      width: 100%;
      min-width: 100%;
      border:none;
      padding: 0 0 0 15px;
      ul[data-depth="1"] {
        margin: 0;
        padding:0;
        > li {
          float: none;
          margin: 0;
          a {
            text-transform: capitalize;
          }
        }
      }      
      ul {
        padding: 0 0 0 15px;
      }
      li > a {
        margin-bottom: 5px;
      }
      ul[data-depth="3"] li a {
        padding-left: rem(40px);
      }
      ul[data-depth="4"] li a {
        padding-left: rem(60px);
      }
    }
    .popover {
      border-radius: 0;
    }
  }
}
#menu-icon {
  color: $light-color;
  padding: 14px 15px;
  font-size: 24px;
  float:left;
  cursor: pointer;
}



