.block_newsletter {  
  font-size: 13px;  
  overflow: hidden;
  display: block;
  background: #f8f8f8;
  padding: 15px;
  p {
    margin:2px 0;
  }
  .conditions {
    display: none;
  }
  .alert-danger , .alert-success{
    padding: 5px;
    margin: 6px 0;
  }
  .valign-wrapper {
    @media (max-width: 991px) {/* If media is below 992 */
      display: block;
    } 
    display: flex;
    align-items: center;
    justify-content: center;
  }
  #newsletter-container {
    @media (max-width: 1390px) {/* If media is below 1300 */
      width: auto;
    }
    @media (max-width: 991px) {/* If media is below 992 */
      text-align: center;
    }   
    overflow: hidden;
    width: 80%;
    margin: 0 auto;
    position: relative;
    padding: 13px 0;
  } 
  .newsletter_text{  
    text-align: center;
    .title_block{ 
      @media (max-width: 543px) {/* If media is below 544 */
        font-size: 18px;
      }
      font-size: 22px;
      font-weight: 500;
      margin: 0;
      letter-spacing: 0;
      line-height: 44px;
      color: $brand-primary;
      position: relative;
      display: inline-block;
      vertical-align: middle;
      padding-left: 60px;
      text-transform: capitalize;
      &:before {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 45px;
        height: 45px;
        background: url('../image/email.png') no-repeat center; 
      }
      span {
        color: $brand-secondary;
      }
    }
    p{
      margin: 0;
      font-size: 14px;
      line-height: 24px;
      display:block; 
      color: #666666;  
      font-weight: 400; 
      margin: 15px 0 16px;  
    }
  }  
  .newsletter_form {
    @media (max-width: 991px) {/* If media is below 992 */
      padding: 0;
      height: auto;
    }
    text-align: center;
    position: relative;
    height: 80px;
    .btn-submit {
      height: 45px;
      cursor: pointer;
      background-color: $brand-secondary;
      font-size: 14px;
      font-weight: 500;
      border: none;
      font-family: $secondary-font;
      text-transform: uppercase;
      border-radius: 0 4px 4px 0;
      color: $brand-primary;
      padding: 14px 20px;
      position: absolute;
      right: 0;
      top: 0;
      -webkit-transition: all 0.4s;
      -moz-transition: all 0.4s;
      -o-transition: all 0.4s;
      transition: all 0.4s;
      &:hover {
        background-color: $brand-primary;
        color: $light-color;
      }
    }
    .text-email {
      min-width: 250px;
      width: 100%;
      height: 45px;
      border-radius: 4px;
      border: 1px solid $border-color;
      color: #666666;
      padding: 10px;
      background-color: $light-color;
      font-family:$secondary-font;
      font-size:12px;
      font-weight:400;
      &:focus {
        background-color:$light-color;
      }
      &:placeholder {
        color: #666666;
      }
    }
  }
  .bannerblock {
    @media (max-width: 991px) {/* If media is below 992 */
      margin-top: 30px;
    }
    .image-container{
      img{
        max-width: 100%;
        width: 100%;
        -webkit-transition: all 0.5s;
              -moz-transition: all 0.5s;
              -o-transition: all 0.5s;
              transition: all 0.5s;     
      }
      a{
        display: block;
        position: relative;
        overflow: hidden;
        -webkit-transition: all 0.5s;
        -moz-transition: all 0.5s;
        -o-transition: all 0.5s;
        transition: all 0.5s;
        &:after {
          content: "";
          position: absolute;
          top: 50%;
          left: 50%;
          right: 50%;
          bottom: 50%;
          -webkit-transition: all 0.9s;
          -moz-transition: all 0.9s;
          -o-transition: all 0.9s;
          transition: all 0.9s;
        }          
      }
      &:hover {
        a {
          &:after {
            background: rgba(0, 0, 0, 0.1);
            transform: rotateY(180deg);
            -moz-transform: rotateY(180deg);
            -webkit-transform: rotateY(180deg);
            -o-transform: rotateY(180deg);
            -ms-transform: rotateY(180deg);
            top: 0;
            left: 0;
            right: 0;
            bottom: 0;
            -webkit-transition: all 0.9s;
            -moz-transition: all 0.9s;
            -o-transition: all 0.9s;
            transition: all 0.9s;
          }
        }
      }
    }
  }
}


