.manufacturerblock {
	@media (min-width: 1200px) {
	  &.container {
	    max-width: 100%;
	    width: 1235px;
	  }
	}
	.home-title {
		display: none;
	}
	.item {
		@media (min-width: 1200px) {/* If media is above 1199 */
			text-align: left;
		}
		text-align: center;
		img {
			opacity: 0.8;
			max-width: 130px;
			display: inline-block;
		}
		&:hover {
			img {
				opacity: 1;
			}
		}
	}
}


