.ishitestimonialsblock {
  text-align: center;
  color:$brand-primary;
  .parallax {
    border: 1px solid $border-color;
    padding: 50px 0 50px;
  }
  .home-title {
    @media (max-width: 543px) {/* If media is below 544 */
      margin-bottom: 20px;
    }
    margin-bottom: 40px;
  }
  .ishitestimonials-container {
    text-align:center;
    .testimonial-img {
      display: inline-block;
      max-width: 150px;
      border-radius: 50%;
      overflow: hidden;
      border: 1px solid $border-color;
      @media (max-width: 991px) {/* If media is below 992 */
        display: block;
        margin: 0 auto;
        text-align: center;
      }
      img{
        max-width: 100%;
      }
    } 
    .user-info {
      display: inline-block;
      vertical-align: middle;
      font: 500 16px $secondary-font;
      color: $brand-primary;
      margin-bottom: 10px;
      .user-name {
        position: relative;
        display: block;
        margin-bottom: 6px;
        text-transform: uppercase;
      }
      .user-designation{
        font-weight: 300;
        color: #666666;
        font-size: 14px;
        display: block;
        margin-bottom: 15px;
      }
      .user-description {
        @media (max-width: 767px) {/* If media is below 768 */
          width: 90%;
        }
        width: 70%;
        margin: 0 auto;
        margin-bottom: 20px;
        position: relative;
        line-height: 25px;
        font-weight: 400;
        color: $black-color;
        font-size: 15px;
      }
    }    
  }
  &:hover {
    .owl-nav {      
      @media (min-width: 544px) {/* If media is above 543 */
        @media (max-width: 991px) {/* If media is below 992 */
          left: 35%;
          right: 35%;
        }
        left: 38%;
        right: 38%;
      }
    }
  }
  .owl-nav {
    -webkit-transition: all 0.5s;
    -moz-transition: all 0.5s;
    -o-transition: all 0.5s;
    transition: all 0.5s;
    margin-top: 15px;
     @media (min-width: 544px) {/* If media is above 543 */
      @media (max-width: 991px) {/* If media is below 992 */
        left: 30%;
        right: 30%;
        bottom: 42%;
      }
      position: absolute;
      left: 35%;
      right: 35%;
      bottom: 50%;
      opacity: 0;
      margin-bottom: -50px;
      .owl-prev, .owl-next {
        position: absolute;
        top: 33%;
        margin-top: 0;
      }
      .owl-prev {
        left: -18px;
      }
      .owl-next {
        right: -18px;
      }
    }
  }
  &:hover {
    .owl-nav {
      @media (min-width: 544px) {/* If media is above 543 */
          opacity: 1;
      }
    }
  }
}


