/* Ishi ProductBlock */
.ishiproductsblock {
  text-align: center;
  .ishiproductstab{
    @media (max-width: 767px) {/* If media is below 768 */
      float: none;
      margin-bottom: 10px;
    }
    position: relative;
    border: 0;
    text-align: center;
    margin: 0 auto;
    margin-bottom: 20px;
    .nav-item { 
      @media (max-width: 543px) {/* If media is below 544 */
        display: block;
        margin-bottom: 8px;
        margin-right: 0;
        text-align: center;
      }
      &:last-child{
        @media (max-width: 543px) {/* If media is below 544 */
          margin-bottom:0px;
        }
      }
      display: inline-block;
      vertical-align: middle;
      margin-right: 5px;
      float: none;
      .nav-link{
        @media (max-width: 767px) {/* If media is below 768 */
          padding: 8px 14px;
        }
        background: transparent none repeat scroll 0 0;
        border: medium none;
        display: inline-block;
        color: #666666;
        font-weight:500;
        font-size: 14px;
        font-family: $secondary-font;
        margin: 0;
        text-transform: uppercase;
        outline: medium none;
        padding: 8px 20px;
        position: relative;
        transition: all 400ms ease-in-out 0s;
        -webkit-transition: all 400ms ease-in-out 0s;
        -moz-transition: all 400ms ease-in-out 0s;
        -o-transition: all 400ms ease-in-out 0s;
        &.active, &:hover{
          color: $brand-third;
        }
      }
      &.active {
        .nav-link{
          color: $brand-third;
        }
      }
    } 
  }
  .tab-content {
    padding: 0;
    margin-top: 10px;    
  }
  .owl-nav {
    .owl-prev, .owl-next {
      position: unset;
      margin-top: 0;
    }
  }
}


