#_desktop_seach_widget {
  @media (min-width: 992px) {/* If media is above 1199 */
    display: inline-block;
    vertical-align: middle;
    float: left;
    position: relative;
    padding: 30px 15px;
  }
}
.search-widget { 
  cursor: pointer;
  position: relative;
  display: inline-block;
  vertical-align: middle;
  @media (max-width: 991px) {/* If media is below 992 */
    padding: 5px;
    float: right;
    position: relative;
  }
  .search-logo{
    @media (max-width: 991px) {/* If media is below 992 */
      background:url('../image/sprite.png') no-repeat center -10px;
      width:40px;
      height:40px;
      display:block;
    }
  }
  form {
    @media (max-width: 991px) {/* If media is below 992 */
      position: absolute;
      right: 0;
      top: 52px;
      display: none;
    }
    display: block;
    z-index: 2;
    position: relative;
    .search-tag {
      @media (max-width: 1199px) {/* If media is below 1200 */
        display: none;
      } 
      background: $brand-secondary;
      color: $brand-primary;
      font-size: 12px;
      font-family: $primary-font;
      height: 40px;
      padding: 10px 15px;
      position: absolute;
      left: 0;
      top: 0;
      z-index: 3;
      display: inline-block;
      vertical-align: middle;
      text-transform: uppercase;
    }
    input[type="text"] {   
      @media (max-width: 1199px) {/* If media is below 1200 */
        min-width: auto;
        text-indent: 0;
      }
      @media (max-width: 991px) {/* If media is below 992 */
        min-width: 300px;
      }   
      min-width: 400px;
      width: 100%;
      border: 1px solid $border-color;
      border-radius: 0;
      height: 40px;
      padding: 5px 10px;
      margin: 0;
      background: $light-color;
      text-transform: capitalize;
      font-size: 13px;
      font-weight: 400;
      text-indent: 150px;
      letter-spacing: 0.8px;
      color: $black-color;
      &:focus{
        @media (max-width: 991px) {/* If media is below 992 */
          background: #f5f5f5;
        }
        background: transparent;
      }
      &::placeholder{
        color: $black-color;
      }
      @media (max-width: 543px) {/* If media is below 544 */
        min-width: 208px;
      }
    }
    .input-group-btn, button {
      position: absolute;
      border: none;
      right: 0;
      top: 0;
      color: $brand-third;
      padding: 0;
      height: 35px;
      width: 40px;
      border-radius: 0;
      background: transparent;
      -webkit-transition: all 0.5s;
      -moz-transition: all 0.5s;
      -o-transition: all 0.5s;
      transition: all 0.5s;
      &:hover, &:focus {
        color: $brand-secondary;
        background: transparent;
      	@media (max-width: 991px) {/* If media is below 992 */
	        background: transparent;
	        i {
	        	color: $brand-primary;
	        }
	     }
      }
      @media (max-width: 991px) {/* If media is below 992 */
        background: transparent;
        i {
        	color: $brand-secondary;
        }
      }
    }
  }
}
#checkout {
  #search_widget {
    display: none; /* Not ideal solution by allows to reuse same hooks/templates */
  }
}

#pagenotfound {
  .page-content {
    #search_widget {
      width: 100%;
    }
  }
}

.page-not-found {
  #_desktop_seach_widget {
    float: none;
    padding: 0;
    position: unset;
  }
  .search-widget {
    position: relative;
    padding: 0;
    .search-logo {
        display: none;
    }
    form {
      display: inline-block;
      top: 0;
      position: relative;
      opacity: 1;
      visibility: visible;
    }
  }
}


