body {
  font-family: $primary-font;
  font-weight: 400;
  color: #232323;
  font-size: 14px;
  line-height: 18px;
  letter-spacing: 0.5px;
}

h1, h2, h3, h4, h5, h6 {
  color: #333333;
}

p {
  color: #888888;
}

/* default font size */

.fa {
  font-size: 14px;
}

/* Override the bootstrap defaults */

h1 {
  font-size: 33px;
}

h2 {
  font-size: 27px;
}

h3 {
  font-size: 21px;
}

h4 {
  font-size: 15px;
}

h5 {
  font-size: 12px;
}

h6 {
  font-size: 10.2px;
}

a {
  color: $brand-primary;
  &:hover {
    color: $brand-secondary;
    text-decoration: none;
    outline: none;
  }
  &:focus {
    outline: none;
    color: $brand-secondary;
    text-decoration: none;
  }
}

ul, .list-unstyled {
  padding: 0;
  list-style: none;
}
.form-control {
  box-shadow: none;
  border-radius: 0;
  margin: 0 0 5px;
  border-color: #e9e9e9;
  -webkit-transition: all 0.5s;
  -moz-transition: all 0.5s;
  -o-transition: all 0.5s;
  transition: all 0.5s;
  &:focus {
    box-shadow: none;
    border-color: #ccc;
  }
}
.form-horizontal {
  .control-label {
    @media (min-width: 768px) {/* If media is above 767 */
      text-align: left;
    }
  }
}

input[type="checkbox"] {
  position: relative;
  top: 1px;
  margin: 4px 0 0px 2px;
}

.panel {
    margin-bottom: 20px;
    background-color: #fff;
    border: 1px solid #e9e9e9;
    border-radius: 0;
    box-shadow: none;
}

legend {
  font-size: 18px;
  padding: 7px 0px;
}

label {
  font-size: 14px;
  font-weight: 400;
  color: #888888;
}

.panel-heading {
  padding: 15px;
}

select.form-control, textarea.form-control {
  font-size: 12px;
}

input {
  &[type="text"].form-control, &[type="password"].form-control, &[type="datetime"].form-control, &[type="datetime-local"].form-control, &[type="date"].form-control, &[type="month"].form-control, &[type="time"].form-control, &[type="week"].form-control, &[type="number"].form-control, &[type="email"].form-control, &[type="url"].form-control, &[type="search"].form-control, &[type="tel"].form-control, &[type="color"].form-control {
    font-size: 12px;
  }
}

.input-group {
  input, select, .dropdown-menu, .popover {
    font-size: 12px;
  }
  .input-group-addon {
    font-size: 12px;
    height: 30px;
  }
}

/* Fix some bootstrap issues */

/*span {
  &.hidden-xs, &.hidden-sm, &.hidden-md, &.hidden-lg {
    display: inline;
  }
}*/
.popover {
    font-family: inherit;
}
.nav-tabs {
  margin-bottom: 15px;
}

div.required .control-label:before {
  content: '* ';
  color: #F00;
  font-weight: bold;
}

/* Gradent to all drop down menus */

.dropdown-menu li > a:hover {
  text-decoration: none;
  outline: none;
  background-color: transparent;
}

@media (max-width: 991px) {/* If media is below 992 */
  #_mobile_column_left, #_mobile_column_right {  
    > #column-left, #column-right {
      float: left;
      width: 100%;
      padding: 0;
      display: block !important;
    }
  }
  #content {
    float: left;
    width: 100%;
  }
}

/* header-before */
#header-before {
  .page-title {
    display: none;
  }
  @media (max-width: 767px) {/* If media is below 768 */
    display: none;
  }
}

.clearfix {
  clear: both;
}

section {
  @media (max-width: 991px) {/* If media is below 992 */
    margin-bottom: 40px;
  }
  margin-bottom: 60px;  
}

/* HEADER*/
  #header {
    color: $brand-primary;
    font-family: $primary-font;
    font-weight: 400;
    font-size: 14px;
    letter-spacing: 0.5px;
    /* header nav*/
    .header-nav {
      background: $light-color;
      border-bottom: 1px solid $border-color;
      .left-nav {
        @media (max-width: 991px) {/* If media is below 992 */
          text-align: center;
        }
        text-align: left;
        > div {
          display: inline-block;
          vertical-align: middle;
        } 
      }
      .right-nav {
        @media (max-width: 991px) {/* If media is below 992 */
          text-align: center;
        }
        text-align: right;
        > div {
          display: inline-block;
          vertical-align: middle;
        }
      }
    }

    /* header top */
    .header-top {
      background: transparent;
      > .container {
        position: relative;
      }
      .menu {
        > ul {
          > li {
            float: none;
          }
        }
        padding-left: 15px;
      }
      .desktop-logo{
        img {
          text-align: center;
          margin: 0 auto;
        }
        @media (max-width: 1199px) {/* If media is below 1200 */
          max-width: 240px;
        }
        @media (max-width: 991px) {/* If media is below 992 */
          position: unset;
        }
        max-width: 350px;
        padding: 25px 15px;
        text-align: center;
        margin: 0 auto;
        position: absolute;
        left: 0;
        right: 0;
        @media (min-width: 1200px) {/* If media is above 1199 */
          &:after, &:before {
            content: '';
            position: absolute;
            top: 25px;
            left: 0;
            width: 1px;
            height: 50px;
            background: $border-color;
          }
          &:before {
            right: 0;
            left: auto;
          }
        }
      }  
    }

    /* Nav full width fixed header */
    .nav-full-width {
      position: relative;
      >.container {
        background-color: $brand-primary;
        position: relative;
      }      
      &.fixed-header {
        background-color: $brand-primary;
        position: fixed;
        top:0; 
        left:0;
        width: 100%; 
        z-index:99;
        animation: slide-down 0.7s;
        -moz-animation: slide-down 0.7s;
        -webkit-animation: slide-down 0.7s;
        -o-animation: slide-down 0.7s;
        overflow: visible;
      }
      @keyframes slide-down {
        0% {
          transform: translateY(-100%);
          -webkit-transform: translateY(-100%);
          -ms-transform:translateY(-100%);
          -o-transform:translateY(-100%);
          -moz-transform:translateY(-100%);
        } 
        100% {
          transform: translateY(0);
          -webkit-transform: translateY(0);
          -ms-transform:translateY(0);
          -o-transform:translateY(0);
          -moz-transform:translateY(0);
        } 
      }
    }      

    /* language, currency and user-info */
    .currency-selector, .language-selector, .user-info {
      padding: 8px;
      white-space: nowrap;
      display: inline-block;
      vertical-align: middle; 
      @media (max-width: 767px) {/* If media is below 768 */
          padding: 8px 3px;
      }
      .dropdown-menu {
        left: auto;
        right: 0;
        top: 29px;
        padding: 5px 7px;
        margin: 0;
        font-size: 14px;
        border-radius: 0;
        @media (max-width: 991px) {/* If media is below 992 */
          top: 26px;
        }
        @media (max-width: 543px) {/* If media is below 544 */
          min-width: 100px;
        }
      }
      li{
        text-decoration: none;
        a, button {
          color: $brand-primary;
          padding: 3px 10px;
          text-align: left;
          font-size: 13px;
          font-weight: 400;
          outline: none;
          &:hover {
            color: $brand-secondary;
          }
        }
      }
      a {
        color: $black-color;
        display: inline-block;
        vertical-align: middle; 
        cursor: pointer;
      }
      .language-selector-label, .currency-selector-label {
        display: none;
      }
    }      

    /* contact info */
    #_desktop_contactinfo {
      display: inline-block;
      vertical-align: middle;
      position: relative;
      padding: 25px 15px;
      cursor: pointer;
      float: right;
      white-space: nowrap;
      @media (max-width: 1199px) {/* If media is below 1200 */
        display: none;
      }
    }
    .contact-num {    
      .call-img {
        background:url('../image/header-sprite.png') no-repeat 11px 10px;
        width: 50px;
        height: 50px;
        border-radius: 50%;
        border: 1px solid $border-color;
        display: inline-block;
        vertical-align: middle;
        -webkit-transition: all 0.5s;
        -moz-transition: all 0.5s;
        -o-transition: all 0.5s;
        transition: all 0.5s;
        @media (max-width: 991px) {/* If media is below 992 */
          background-position: 11px 10px;
          width: 40px;
          height: 40px;
          border-radius: 0;
          border: 0;
        }
      }
      .call {
        display: inline-block;
        vertical-align: middle;
        padding-left: 10px;
        .call-us {
          display: block;
          text-transform:uppercase;
          position: relative;
          font-weight: 500;
        }
        .call-num {
          font-size: 13px;
          color: #666666;
          margin-top: 2px;
        }
      }
      &:hover {
        .call-img {
          background-position: 11px -48px;
        }
      }
    }

    /* cart */
    #_desktop_cart {
      display: inline-block;
      vertical-align: middle;
      position: relative;
      padding: 25px 15px;
      cursor: pointer;
      float: right;
      white-space: nowrap;
      @media (max-width: 991px) {/* If media is below 992 */
        padding: 0;     
      }
    }
    .blockcart {
      @media (max-width: 991px) {/* If media is below 992 */
        padding: 5px 0;   
        padding-right: 15px; 
        position: relative;
      }
      cursor: pointer;
      display: inline-block;
      vertical-align: middle;
      text-align: left;
      white-space: nowrap;
      -webkit-transition: all 0.5s;
      -moz-transition: all 0.5s;
      -o-transition: all 0.5s;
      transition: all 0.5s;
      &:hover {
        .cart-img {
          @media (max-width: 991px) {/* If media is below 992 */
            background-position: center -49px;
          }
          background-position: -61px -48px;
        }
      }
      .cart-link {
        display: block;
        width: 100%;
        overflow: hidden;
      }
      .cart-img{
        background:url('../image/header-sprite.png') no-repeat -61px 13px;
        width: 50px;
        height: 50px;
        border-radius: 50%;
        border: 1px solid $border-color;
        display: inline-block;
        vertical-align: middle;
        -webkit-transition: all 0.5s;
        -moz-transition: all 0.5s;
        -o-transition: all 0.5s;
        transition: all 0.5s;
        @media (max-width: 991px) {/* If media is below 992 */
          background:url('../image/sprite.png') no-repeat center -49px;
          width: 40px;
          height: 40px;
          border-radius: 0;
          border: 0;
        }
      }
      .cart-content {
        display: inline-block;
        vertical-align: middle;
        padding-left: 10px;
        @media (max-width: 991px) {/* If media is below 992 */
          padding-left: 0;
        }
        .cart-name{
          display: block;
          text-transform:uppercase;
          position: relative;
          font-weight: 500;
          @media (max-width: 991px) {/* If media is below 992 */
            display: none;
          }
        }
        .cart-products-count {
          line-height: 14px;     
          display: block;
          width: auto;
          margin-top: 2px;
          letter-spacing: 1px;
          text-transform: capitalize;         
          font-size: 13px;
          color: #666666;
          text-align: left;
          @media (max-width: 991px) {/* If media is below 992 */
            border-radius: 50%;
            height: 14px;
            width: 14px;
            background: $light-color;
            color: $brand-primary;
            font-size: 11px;
            right: 5px;
            top: 7px;
            position: absolute;
            padding: 1px 4px;
            margin: 0;
            text-align: center;
          }        
        }
      }
      #cart {
        table > tbody > tr > td {
          white-space: normal;
        }          
        > .btn {
          font-size: 14px;
          line-height: 18px;
          color: #000000;
          background: transparent;
          border: 0;
          border-radius: 0;
          box-shadow: none;
          text-shadow: none;
        }
        &.open > .btn {
          color: #000000;
          &:hover {
            color: #000000;
          }
        }
        .cart-dropdown, .dropdown-menu {
          @media (max-width: 991px) {/* If media is below 992 */
            top: 47px;
          }
          @media (max-width: 767px) {/* If media is below 768 */
            width: 300px;
          }
          font-family: $secondary-font;
          display: none;
          left: auto;
          position: absolute;
          right: 0;
          text-align: left;
          width: 350px;
          z-index: 3;
          border: 1px solid $border-color;
          top: 60px;
          padding: 0 15px;
          background: #ffffff;
          table {
            margin-bottom: 10px;
            width: 100%;
            max-width: 350px;
          }
          .cart-btn {
            padding: 10px;
            .btn-cartblock {
              padding: 7px 15px;
            }
          }        
          li {
            text-decoration: none;
            outline: none;
            list-style: none;
            .empty {
              font-size: 14px;
              line-height: 18px;
              margin-bottom: 0;
              padding: 10px 0;
              color: $brand-primary;
            }
          }
        }
      }    
      button, .btn-block {
        padding: 0;
      }
    } 

    /* Mobile Media Query Start */
    #_mobile_user_info, #_mobile_seach_widget, #_mobile_cart, #_mobile_link_menu {
      float: right;
    }
    /* Mobile Media Query End */
  }
/* HEADER END*/

/* alert */

.alert {
  padding: 12px 14px 12px 14px;
  text-align: center;
  border-radius: 0; 
  button {
    display: block;
    overflow: hidden;
    position: relative;
    &.close {
      position: relative;
      top: 2px;
      right: 0;
      margin: 0;
    }
  }
}

/* buttons */
.buttons {
  margin: 1em 0;
  display: block;
  overflow: hidden;
  position: relative;
  &.close {
    position: relative;
    top: 2px;
    right: 0;
  }
}

.alert-dismissible {
  .close {
    position: relative;
    top: 2px;
    right: 0;
  }
}

.btn {
  padding: 7.5px 12px;
  font-size: 12px;
  line-height: 18px;
  border-radius: 0;
  &.active {
    box-shadow: none;
    outline: none;
  }
  &:focus {
    outline: none;
    text-decoration: none;
  }
}

.btn-xs {
  font-size: 9px;
}

.btn-sm {
  font-size: 10.2px;
}

.btn-lg {
  padding: 10px 16px;
  font-size: 15px;
}

.btn-group > {
  .btn, .dropdown-menu, .popover {
    font-size: 12px;
    &:focus {
      outline: none;
      text-decoration: none;
    }
  }
  .btn-xs {
    font-size: 9px;
  }
  .btn-sm {
    font-size: 10.2px;
  }
  .btn-lg {
    font-size: 15px;
  }  
}
.btn-warning, .btn-inverse, .btn-link, .btn-info, .btn-success, .btn-danger, .btn-warning {
  padding: 7.5px 12px;  
  &:hover, &:focus {
    outline: none;
    text-decoration: none;
  }
}

.btn-default {
  background-color: $brand-secondary;
  border-color: $brand-secondary;
  color: $brand-primary;
  outline: none;
  line-height: 20px;
  font-weight: 400;
  padding: 7px 20px;
  text-transform: uppercase;
  border-radius: 0;
  -webkit-transition: all 0.5s;
  -moz-transition: all 0.5s;
  -o-transition: all 0.5s;
  transition: all 0.5s;
  &:hover, &:active, &:focus{
    color: $light-color;
    background-color:$brand-primary;
    border-color:$brand-primary;
    outline: none;
  }
  &:active{
    &:hover{
      color:$light-color;
      background-color:$brand-primary;
      border-color:$brand-primary;
      outline: none;
    }
    &:focus{
      color:$light-color;
      background-color:$brand-primary;
      border-color:$brand-primary;
      outline: none;
    }
  }
}

.btn-primary,
.btn.disabled,
.btn:disabled,
.btn-secondary  {
  opacity: 1;
  font-weight: 400;
  padding: 7px 20px;
  text-transform: uppercase;
  border-radius: 0;
  -webkit-transition: all 0.5s;
  -moz-transition: all 0.5s;
  -o-transition: all 0.5s;
  transition: all 0.5s;
  color:$light-color;
  background-color: $brand-primary;
  border:1px solid $brand-primary;
  outline: none;
  line-height: 20px;
  &:hover, &:active, &:focus{
    color: $brand-primary;
    background-color:$brand-secondary;
    border-color:$brand-secondary;
    outline: none;
  }
  &:active{
    &:hover{
      color:$brand-primary;
      background-color:$brand-secondary;
      border-color:$brand-secondary;
      outline: none;
    }
    &:focus{
      color:$brand-primary;
      background-color:$brand-secondary;
      border-color:$brand-secondary;
      outline: none;
    }
  }
}

/* list group */

.list-group a {
  border: 1px solid #e9e9e9;
  color: #888888;
  padding: 10px 12px;
  &.active {
    color: #444444;
    background: #eeeeee;
    border: 1px solid #DDDDDD;
    text-shadow: 0 1px 0 #FFF;
    &:hover {
      color: #444444;
      background: #eeeeee;
      border: 1px solid #DDDDDD;
      text-shadow: 0 1px 0 #FFF;
    }
  }
  &:hover {
    color: #444444;
    background: #eeeeee;
    border: 1px solid #DDDDDD;
    text-shadow: 0 1px 0 #FFF;
  }
  &:focus {
    color: #888888;
    background: #eeeeee;
    border: 1px solid #DDDDDD;
    text-shadow: 0 1px 0 #FFF;
  }
}

