/* fixed colum left + content + right*/
@media (min-width: 768px) {
  #column-left {
    .product-layout .col-md-3 {
      width: 100%;
    }
    + #content {
      .product-layout .col-md-3 {
        width: 50%;
      }
      + #column-right .product-layout .col-md-3 {
        width: 100%;
      }
    }
  }
  #content + #column-right .product-layout .col-md-3 {
    width: 100%;
  }
}



/* fixed product layouts used in left and right columns */ 
@media (min-width: 1200px) {/* If media is above 1199 */
  .collapse {
      display: inherit;
  }
}
@media (min-width: 992px) {/* If media is above 991 */
  .collapse {
      display: inherit;
  }
}
.block-title,.footer-title,.title {
    position: relative;
    cursor:pointer;
    .navbar-toggler {
        position: absolute;
        top:12px;
        right: 15px;
        padding:0;
        font-size: 22px;
        font-style: normal;
        display: inline-block;
        vertical-align: middle;
        width: 1em;
        height: 1em;
        text-align: center;
    }
}
.block-title,.footer-title {
  .navbar-toggler {
      i {
        &.add {
          display: none;
        }
        &.remove {
          display: block;
        }
      }
  }
  &.collapsed{
    .navbar-toggler {
      i {
        &.add {
          display: block;
        }
        &.remove {
          display: none;
        }
      }
    }
  }
}


#column-left, #column-right {  
  .block-title {
    .h3 {
      border: medium none;
      color: #fff;
      font: 400 16px/18px $primary-font;
      letter-spacing: 1px;
      width: 100%;
      display: block;
      margin: 0;
      text-align: left;
      background: #000;
      padding: 15px;
      text-transform: uppercase !important;
    } 
    .navbar-toggler {
      top: 13px;
      right: 15px;
      color: $light-color;
    }   
  }
  .data-toggler {
    > div {
      display: block;
    }
  }

  .swiper-viewport {
