/*========================== NO CHANGE ==========================*/
   
#siteloader {
  height: 100%;
  left: 0;
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 9999;
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
  .loader {
    margin: 0 auto;
    width: 50px;
    height: 50px;
    position: relative;
    text-align: center;    
    -webkit-animation: sk-rotate 2.0s infinite linear;
    animation: sk-rotate 2.0s infinite linear;
    .dot1, .dot2 {
      width: 60%;
      height: 60%;
      display: inline-block;
      position: absolute;
      top: 0;
      background-color: $brand-secondary;
      border-radius: 100%;      
      -webkit-animation: sk-bounce 2.0s infinite ease-in-out;
      animation: sk-bounce 2.0s infinite ease-in-out;
    }
    .dot2 {
      top: auto;
      bottom: 0;
      -webkit-animation-delay: -1.0s;
      animation-delay: -1.0s;
    }
  }
}

@-webkit-keyframes sk-rotate {
   100% { 
   -webkit-transform: rotate(360deg) 
   }
}
@keyframes sk-rotate { 
  100% {
    transform: rotate(360deg);
    -webkit-transform: rotate(360deg) 
  }
}

@-webkit-keyframes sk-bounce {
  0%, 100% { 
  -webkit-transform: scale(0.0) 
  }
  50% {
   -webkit-transform: scale(1.0) 
  }
}

@keyframes sk-bounce {
  0%, 100% { 
    transform: scale(0.0);
    -webkit-transform: scale(0.0);
  } 50% { 
    transform: scale(1.0);
    -webkit-transform: scale(1.0);
  }
}

#spin-wrapper {
  width: 100%;
  height: 100%;
  position: fixed;
  z-index: 9998;
  background: #fff;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}


