.ishistoreinfoblock{
	text-align: left;
	display: block;
	overflow: hidden;
	.ishistoreinfo-inner{
		.store-logo{
			@media (min-width: 1200px) {/* If media is above 1199 */
				width: 88%;
			}
			display:block;
			margin-bottom: 20px;
			text-align: center;
			img {
				max-width: 100%;
			}
			&:after {
				display: none;
			}
		}
		.store-description{
		    margin-bottom: 0;
		    p{
				line-height: 24px;
				font-size: 14px;
				font-family: $primary-font;
				margin-bottom:0;
				color: $black-color;
				@media (min-width: 1200px) {/* If media is above 1199 */
					width: 88%;
				}
		    }
		}
	}
}


