#checkout-checkout {
	#accordion {
		.panel-default {
			border-radius: 0;
			overflow: hidden;
			.panel-heading {
	    		border-color: #e9e9e9;
	    		.panel-title {
	    			a{
	    				display: block;
	    				i{
	    					float: right;
	    				}
	    			}	    			
	    		}
	    	}
	    }
	    h2 {
			font-size: 23px;
			line-height: 25px;
			font-weight: 500;
		}
	}
	@media (min-width: 1200px) {/* If media is above 1199 */
	  	.collapse {
	        display: none;
	        &.in {
	          display: block;
	        }
	    }
	}
	@media (min-width: 992px) {/* If media is above 991 */
		.collapse {
	        display: none;
	        &.in {
	          display: block;
	        }
	    }
	}
}


