.owl-nav {
	text-align: center;
	-webkit-transition: all 0.5s;
	-moz-transition: all 0.5s;
	-o-transition: all 0.5s;
	transition: all 0.5s;
	.owl-prev, .owl-next {
		@media (min-width: 544px) {/* If media is above 543 */
			@media screen and (min-width: 1199px) and (max-width: 1400px) {/* If media is between 1199 and 1400 */
				left: -2px;
			}
			position: absolute;
			top: 50%;
			margin-top: -45px;
			left: -5px;
		}
		display: inline-block;
		vertical-align: middle;
		border: 1px solid #e7e7e7 !important;
		background: $light-color !important;
    	border-radius: 50%;
    	margin:4px;
    	outline: none !important;
    	width: 35px;
    	height:35px;    	
    	&:hover{
    		background-color: $light-color !important;
    		border-color: #e7e7e7 !important;
    		i{
    			&:before{
    				color: $brand-third !important;
    			}
    		}
    	}
    }
    .owl-next {
    	@media (min-width: 544px) {/* If media is above 543 */
    		@media screen and (min-width: 1199px) and (max-width: 1400px) {/* If media is between 1199 and 1400 */
				right: -2px;
			}
    		right: -5px;
    		left: auto;	
    	}
    }
	i{
		&:before {
			color: $black-color;
			position: relative;
			text-align: center;
			font-size: 12px;
			line-height: 24px;
			text-align: center;
			margin: 0 auto;
		}
	}
}


