.smartblog_block {
	display: block;
	position: relative;
	text-align: center;
	.blog_post{
		&:hover{
			.news_module_image_holder{
				position: relative;
				img{
					-webkit-transform-style:unset;
					transform-style:unset;
				}
				.blogicons{
					opacity: 1;	
				}
				.date-time {
					-moz-transform:translateY(20px);  
					-webkit-transform:translateY(20px);  
					-o-transform:translateY(20px);  
					-ms-transform:translateY(20px); 
					transform:translateY(20px);
				}
				.write-comment {
					a {
						opacity: 0;
						-webkit-transition: all 0.5s;
						-moz-transition: all 0.5s;
						-o-transition: all 0.5s;
						transition: all 0.5s;
					}
				}
			}
			.blog_content {
				.view-blog {
					.read-more {
						font-size: 14px;
						transform: scale(1);
						-moz-transform: scale(1);
						-webkit-transform: scale(1);
						-o-transform: scale(1);
						-ms-transform: scale(1);
					}
					margin-right: 8px;
				}
			}		
		}
		position: relative;
		display: inline-block;
		vertical-align: middle;
		overflow: hidden;
		margin: 15px 15px 20px;	
		box-shadow: 1px 1px 4px 1px rgba(0,0,0,.1);
		.news_module_image_holder{
			display: block;
			max-width: 100%;
			position: relative;
			background: $light-color;
			opacity: 0.8;
			img{
				width: 100%;
				transition: all 0.5s;
				-webkit-transition: all 0.5s;
				-moz-transition: all 0.5s;
				-ms-transition: all 0.5s;
				-o-transition: all 0.5s;
				-webkit-transform-style:unset;
				transform-style:unset;
			}
			.blogicons {
				display: none;
			}
			.date-time{
				position: absolute;
				bottom: 11px;
				background: transparent;
				left: 0;
				right: 0;
				padding: 12px 0;
				color: $brand-primary;
				font-size: 30px;
				font-weight: 500;
				font-family: $secondary-font;
				z-index: 2;
				-webkit-transition: all 0.5s;
				-moz-transition: all 0.5s;
				-o-transition: all 0.5s;
				transition: all 0.5s;
				i{
					display: none;
					vertical-align: middle;
					font-size: 16px;
					line-height: 18px;
					color: $brand-secondary;
					padding-right: 5px;
				}
			}
			.author{
				display: block;
			}
			.write-comment {
				@media (max-width: 767px) {/* If media is below 768 */
					left: 10px;
					right: 10px;
				}
			    position: absolute;
			    background: #fff;
			    color: $brand-third;
			    bottom: -20px;
			    padding: 20px 15px 15px;
			    left: 50px;
			    right: 50px;
			    text-align: center;
			    margin: 0 auto;
			    font-family: $secondary-font;
			    display: inline-block;
				vertical-align: middle;
			    a{
				   color: $brand-third;
				}
			}
		}
		.blog_content{
			padding:20px 10px;
			text-align: center;
			.blog_title{
				margin-bottom: 10px;
				margin-top: 0;
				a{
					font-family: $secondary-font;
					font-size: 16px;
					text-transform: capitalize;
					font-weight: 500;
				}
			}
			.blog-desc{
				margin-bottom: 0;
				font-family: $secondary-font;
				font-size: 14px;
				color: $black-color;
			}
			.view-blog{
				display: inline-block;
				vertical-align: middle;				
				margin: 10px;
				overflow: hidden;
				color: $brand-secondary;
				text-transform: capitalize;
				font-family: $secondary-font;
				-webkit-transition: all 0.5s;
				-moz-transition: all 0.5s;
				-o-transition: all 0.5s;
				transition: all 0.5s;				
				.read-more {
					font-size: 0;
					display: inline-block;
					vertical-align: middle;
					transform: scale(0);
					-moz-transform: scale(0);
					-webkit-transform: scale(0);
					-o-transform: scale(0);
					-ms-transform: scale(0);
					-webkit-transition: all 0.5s;
					-moz-transition: all 0.5s;
					-o-transition: all 0.5s;
					transition: all 0.5s;
				}
				a {
					color: $brand-secondary;
				}
				i {
					font-size: 19px;
					display: inline-block;
					vertical-align: middle;
					text-align: center;
					color: $brand-secondary;
				}
			}			
		}
	}
	.owl-nav {
		@media (min-width: 544px) {/* If media is above 543 */
			opacity: 0;
		}
	}
	&:hover {
	    .owl-nav {
	     	@media (min-width: 544px) {/* If media is above 543 */
	          	opacity: 1;
	      	}
	    }
	}
}

/*<------------------------------------------------------------------------------------>*/
.single-blog{
	text-align: center;
	.page-item-title{
		margin-bottom: 30px;
		h1{
			font-size: 22px;
			text-transform: uppercase;
			color: $brand-primary;
			font-weight: 500;
			font-family: $primary-font;
			margin: 0;
		}
	}
	.blog-img{		
		img{
			max-width: 100%;
			height: auto;
			text-align: center;
			margin: 0 auto;
		}
	}
	.blog-date {
		display: none;
		font-size: 15px;
		position: relative;
		text-transform: capitalize;
		color: #000;
		font-family: $primary-font;
		padding: 20px 0 0;
	}
	.blog-desc{
		@media (max-width: 991px) {/* If media is below 992 */
			width: 100%;
		}
		padding: 30px 15px 20px;
		font-family: $primary-font;
		margin: 0 auto;
		width: 80%;
		color: #332233;
		font-weight: 300;
		font-size: 14px;
		letter-spacing: .8px;
		line-height: 20px;
	}
	.smartblogcomments{
		margin-bottom: 50px;
		.reply-title{
			margin-bottom: 15px;
			font-weight: 500;
			color: $brand-primary;
			font-family: $primary-font;
			font-size: 20px;
		}
		.view-comment {
			text-align: left;
			padding: 15px 0;
			margin-bottom: 15px;
			border-top: 1px solid #eeeeee;
			border-bottom: 1px solid #eeeeee;
			&:last-child {
				margin-bottom: 0;
			}
			.user_icon {
				display: inline-block;
				vertical-align: middle;
				i {
					&:before {
						font-size: 70px;
						color: #000000;
					}
				}
			}
			.user_list {
				display: inline-block;
				vertical-align: middle;
				text-align: left;
				margin-left: 10px;
			}
		}
	}
	.block-title {
		.reply-title{
			margin-bottom: 15px;
			font-weight: 500;
			color: $brand-primary;
			font-family: $primary-font;
			font-size: 20px;
		}
	}
	#add-comment {
		.form-horizontal {
			.control-label {
			    text-align: left;
			}
			.form-control {
				border-color: #eeeeee;
				&:last-child {
					margin-bottom: 0;
				}
			}
			.form-group {
				margin: 15px;
			}
			.submit-btn {
				margin-top: 20px;
			}
		}
	}
}

/*<------------------------------------------------------------------------------------>*/
.all-blog {
	text-align: center;	
	margin: 0 auto;
	h1 {
		display: none;
	}
	.sdsarticleCat{
		display: block;
		overflow: hidden;
		margin-bottom: 30px;
		text-align: center;		
		> div{
			font-family: $primary-font;
			font-size: 14px;
			color:#000;
			font-weight: 400;		
			line-height: 20px;	
		}
		.blog-content {
			margin-bottom: 30px;
			display: block;
			overflow: hidden;
			@media (max-width: 991px) {/* If media is below 992 */
				margin-bottom: 20px;
			}
		}
		.articleContent {
			&:last-child{
				margin-bottom: 0;
			}
			.post_hover {
				display: none;
			}
			.blog-image {
				img {
					text-align: center;
					margin: 0 auto;
				}
			}
		}
		.smartblog-desc {
			position: relative;
			margin-top: 15px;
			.blog-date-comment {
				@media (max-width: 1199px) {/* If media is below 1200 */
					margin-bottom: 10px;
				}
				margin-bottom: 15px;
				.blog-title{
					@media (max-width: 1199px) {/* If media is below 1200 */
						margin: 15px 0;
					}
					margin: 0 0 15px;		
					a{
						font-size: 20px;
						text-transform: uppercase;
						color: #000000;
						font-weight: 400;
						text-decoration: none;
					}
				}
				.blog-date {
					display: block;
					margin-bottom: 15px;
					font-size: 15px;
					position: relative;
				}
				.comment-wrapper {
					display: block;
					span{
						font-size: 15px;
						display: inline-block;
						vertical-align: middle;
						margin-right: 10px;
						position: relative;
						text-transform: capitalize;
						color: #000000;
						line-height: 25px;
					}
				}
			}
			.blog-desc {
				@media (max-width: 1199px) {/* If media is below 1200 */
					margin-bottom: 10px;
				}
				display: block;
				margin-bottom: 15px;
				color: #666;
				line-height: 24px;
				font-family: $secondary-font;
			}
			.read-more{
				a{
					text-decoration: none;
					font-size: 14px;
					font-weight: 400;
					background: $brand-secondary;
					color: $brand-primary;
					text-transform: uppercase;
					padding: 5px 15px;
					border-radius: 4px;
					display: inline-block;
					vertical-align: middle;
					-webkit-transition: all 0.5s;
					-moz-transition: all 0.5s;
					-o-transition: all 0.5s;
					transition: all 0.5s;
					&:hover {
						background: $brand-primary;
						color: $light-color;
					}
				}
			}
		}
	}
}


