.ishiparallaxbannerblock {
	overflow: hidden;
	.parallax {
		@media (max-width: 1199px) {/* If media is below 1200 */
			padding: 50px 0;
		}
		padding: 120px 0;
		.parallax-list {
			display: inline-block;
			vertical-align: middle;
			text-align: center;
			float: left;
			font-family: $secondary-font;
			@media (max-width: 767px) {/* If media is below 768 */
				display: block;
				text-align: center;
			}
			.parallax-title{
				@media (max-width: 543px) {/* If media is below 768 */
					font-size: 28px;
					line-height:30px;
				}
				font-size: 40px;
				font-style: italic;
				font-weight: 300;
				line-height: 40px;
				margin-bottom: 25px;
				color: $brand-primary;
				text-transform:capitalize;
			}
			.parallax-subtitle{
				font-size: 70px;
				font-weight: 400;
				color: #010101;
				line-height: 75px;
				margin-bottom: 15px;
				text-transform:capitalize;
				@media (max-width: 767px) {/* If media is below 768 */
					font-size: 50px;
				}
				@media (max-width: 543px) {/* If media is below 768 */
					font-size: 35px;
					line-height:40px;
				}
				span {
					font-weight: 500;
				}
			}
			.parallax-offer{
				font-size: 100px;
				line-height: 80px;
				font-weight: 600;
				margin-bottom: 30px;
				position: relative;
				color: $brand-third;
			}
			.parallax-desc{
				font-size: 22px;
				line-height: 30px;
				font-weight: 400;
				margin-bottom: 30px;
				position: relative;
				color: $brand-primary;
			}
			> a{
				font-size: 16px;
				color: $brand-primary;
				background-color: $brand-secondary;
				border: 1px solid $brand-secondary;
				font-weight:400;
				font-family: $primary-font;    
				padding: 10px 20px;
				position: relative;
				text-transform: uppercase;
				cursor: pointer;  
				-webkit-transition: 0.5s all ease;
				-moz-transition: 0.5s all ease;
				-o-transition: 0.5s all ease;	
				transition: 0.5s all ease;
				@media (max-width: 543px){
					padding: 6px 10px;
				}
				&:hover {
					border-color: $brand-primary;  
					background: $brand-primary;
					color: $light-color;
				}
			}
		}
	}
}


