.product-thumb {
  @media (max-width: 543px) {/* If media is below 544 */
    max-width: 370px;
    margin: auto;
  }
  text-align: center;
  padding: 15px;
  display: block;
  overflow: hidden;
  position: relative;
  .image {
    overflow: hidden;    
    position: relative;
    a{
      display: block;
      position: relative;
    }
    img {
      margin: 0 auto;
      max-width: 100%;
      -webkit-transition: all 0.7s ease 0s;
      -moz-transition: all 0.7s ease 0s;
      -ms-transition: all 0.7s ease 0s;
      -o-transition: all 0.7s ease 0s;
      transition: all 0.7s ease 0s;
    }
    .product-img-extra, &.change {
      left: 0;
      position: absolute;
      right: 0;
      top: 0;
      opacity: 0;
      -moz-transform: scale(0,1);
      -webkit-transform: scale(0,1);
      -o-transform: scale(0,1);
      -ms-transform: scale(0,1);
      transform: scale(0,1);
    }
  }
  .caption {
    padding: 10px;
    h4 {
      font: 400 14px/18px $primary-font;
      overflow: hidden;
      margin: 0;
      text-transform: capitalize;
      margin-bottom: 10px;
      a{
        color: #202020;
        white-space: nowrap;
      }
    }
    .description {
      display: none;
    }
    .price {
      font-family: $primary-font;
      color: $brand-primary;
      font-size: 14px;
      font-weight: 500;
      margin-bottom: 0;
      .price-old {
        color: #888;
        margin-left: 5px;
        text-decoration: line-through;
      }
      .price-tax {
        color: #999;
        font-size: 12px;
        display: none;
      }
    }
  }  
  .rating {
    position: absolute;
    bottom: 10px;
    left: 0;
    right: 0;
    text-align: center;
    margin: 0 auto;
    .fa-star, .fa-star-o {
        color: $brand-secondary;
        font-size: 13px;
        line-height: 0;
    }
    .fa-star-o {
      color: #999;
    }
    span{
      &.fa-stack {              
        width: 8px;
        height: 8px;
        margin-right: 3px;
      }
    }
  }
  .button-group {    
    width: 30px;
    height: 30px;
    position: absolute;
    top: 5px;
    right: 5px;
    display: inline-block;
    vertical-align: middle;
    -webkit-transition: all .5s;
    -moz-transition: all .5s;
    -o-transition: all .5s;
    transition: all .5s;
    i {
      display: none;
    }  
    > div {    
      border: none;
      border-radius: 0;
      display: inline-block;      
      vertical-align: middle;
      line-height: 0;
      text-align: center;      
      button {
        background: url(../image/product-sprite.png) no-repeat;
        width: 30px;
        height: 30px;
        border: none;
        border-radius: 0;
        padding: 0;
        span {
          display: none !important;
        }
      }
    }
    .btn-quickview, .btn-wishlist, .btn-compare {
      @media (min-width: 543px) {/* If media is above 544 */
        opacity: 0;
      }
    }
    .btn-quickview {
      @media (min-width: 543px) {/* If media is above 544 */
        opacity: 0;
        transform: translateY(50px);
        -moz-transform:translateY(50px);  
        -webkit-transform:translateY(50px);  
        -o-transform:translateY(50px);  
        -ms-transform:translateY(50px);
      }
      -webkit-transition: all ease-in-out .2;
      -moz-transition: all ease-in-out .2;
      -o-transition: all ease-in-out .2;
      transition: all ease-in-out .2s; 
      .quickbox {
        background: url(../image/product-sprite.png) no-repeat 4px -131px;
        width: 30px;
        height: 30px;
        border: none;
        border-radius: 0;
        padding: 0;      
        display: inline-block;
        vertical-align: middle;
      }
      &:hover {
        .quickbox {
          background-position: -39px -131px;
        }
      }
    }
    .btn-wishlist {
      @media (min-width: 543px) {/* If media is above 544 */
        opacity: 0;
        transform: translateY(50px);
        -moz-transform:translateY(50px);  
        -webkit-transform:translateY(50px);  
        -o-transform:translateY(50px);  
        -ms-transform:translateY(50px);
      }
      -webkit-transition: all ease-in-out .4s;
      -moz-transition: all ease-in-out .4s;
      -o-transition: all ease-in-out .4s;
      transition: all ease-in-out .4s;
      button {
        background-position: 3px 2px;
      }
      &:hover {
        button {
          background-position: -40px 2px;
        }
      }
    }
    .btn-compare {
      @media (min-width: 543px) {/* If media is above 544 */
        opacity: 0;
        transform: translateY(50px);
        -moz-transform:translateY(50px);  
        -webkit-transform:translateY(50px);  
        -o-transform:translateY(50px);  
        -ms-transform:translateY(50px);
      }
      -webkit-transition: all ease-in-out .6s;
      -moz-transition: all ease-in-out .6s;
      -o-transition: all ease-in-out .6s;
      transition: all ease-in-out .6s;
      button {
        background-position: 3px -89px;
      }
      &:hover {
        button {
          background-position: -40px -89px;
        }
      }
    }         
  }
  .btn-cart {
    display: inline-block;
    vertical-align: middle;
    position: absolute;
    bottom: 10px;
    left: 0;
    right: 0;
    margin: 0 auto;
    text-align: center;
    overflow: hidden;
    -webkit-transition: all .5s;
    -moz-transition: all .5s;
    -o-transition: all .5s;
    transition: all .5s;
    opacity: 0;
    -moz-transform:translateY(50px);  
    -webkit-transform:translateY(50px);  
    -o-transform:translateY(50px);  
    -ms-transform:translateY(50px);
    -webkit-transition: all .6s;
    -moz-transition: all .6s;
    -o-transition: all .6s;
    transition: all .6s;
    button {
      background: $brand-secondary url(../image/add-to-cart.png) no-repeat 6px 11px;
      border: none;
      color: $brand-primary;
      font-size: 13px;
      font-weight: 400;
      position: relative;
      border-radius: 0;
      letter-spacing: 0.5px;
      line-height: 24px;
      text-transform: uppercase;
      padding: 7px 10px 7px 30px;  
      -webkit-transition: all .5s;
      -moz-transition: all .5s;
      -o-transition: all .5s;
      transition: all .5s;
      .lblcart{
        display: block;
      }
      i {
        color: $brand-secondary;
        display: none;
      }
    }      
    &:hover{      
      button{
        background-position: 6px -27px;
        background-color: $brand-third;
        color: $light-color;
      }
    }
  }
  &:hover {
    .rating {
      opacity: 0;
    }
    .button-group {
      .btn-quickview, .btn-wishlist, .btn-compare {
        opacity: 1;
        -moz-transform:translateY(0);  
        -webkit-transform:translateY(0);  
        -o-transform:translateY(0);  
        -ms-transform:translateY(0);
      }
    }
    .image {
      .product-img-extra{
        opacity: 1;
        -moz-transform: scale(1,1);
        -webkit-transform: scale(1,1);
        -o-transform: scale(1,1);
        -ms-transform: scale(1,1);
        transform: scale(1,1);
      }
    }
    .btn-cart {
      opacity: 1;
      -moz-transform:translateY(0);  
      -webkit-transform:translateY(0);  
      -o-transform:translateY(0);  
      -ms-transform:translateY(0);
    }
  }
}
.product-grid {
  @media (max-width: 767px) {/* If media is below 768 */
    width: 50%;
  }
    @media (max-width: 543px) {/* If media is below 544 */
    margin: 0 auto;
    width: 300px;
      float: none;
    }
    .product-thumb {
      padding: 15px 0;
    }
  }
.product-list {
  padding: 0;
    @media (max-width: 543px) {/* If media is below 544 */
    margin: auto;
    width: 300px;
    float: none;
  }
    .product-thumb {
      @media (max-width: 543px) {/* If media is below 544 */
        text-align: center;
      }
      text-align: left;
    padding: 15px 0;
      .image {
      @media (max-width: 767px) {/* If media is below 768 */
       width: 50%;
        }
     @media (max-width: 543px) {
      margin-bottom: 15px;
          width: 100%;
        }
      } 
   .button-group {
      right: 20px;
    }
      .caption {
      @media (max-width: 767px) {/* If media is below 768 */
        width: 50%;
      }
      @media (max-width: 543px) {
        width: 100%;
      }
      padding: 15px;
        h4 {
          padding: 0;
          margin-bottom: 12px;
        }
        .description {
          display: block;
          margin-bottom: 12px;
          font-size: 14px;
          line-height: 20px;
        }
        .rating {
          text-align: left;
        }
        .price {
          margin-bottom: 12px;
          opacity: 1;
        }
        .btn-cart {
          position: relative;
          bottom: 0;
          opacity: 1;
          -moz-transform: translateY(0);
          -webkit-transform: translateY(0);
          -o-transform: translateY(0);
          -ms-transform: translateY(0); 
        }
      } 
    }
}


