.ishicategoryblock{
	text-align: center;
	overflow: hidden;
	position: relative;
	.ishicategory {
		text-align: center;
		overflow: hidden;
		position: relative;
		.image-container{
			display: block;
			position: relative;
			overflow: hidden;
			-webkit-transition: all 0.5s;
		    -moz-transition: all 0.5s;
		    -o-transition: all 0.5s;
		    transition: all 0.5s;
			img{
				max-width: 100%;
				-webkit-transition: all 0.5s;
	            -moz-transition: all 0.5s;
	            -o-transition: all 0.5s;
	            transition: all 0.5s;			
			}			
			&:hover {
				.text-container	{
					span {
						color: $brand-primary;
		    			background: $brand-secondary;
					}
				    -moz-transform:translateY(-80px) rotateX(360deg);  
					-webkit-transform:translateY(-80px) rotateX(360deg);  
					-o-transform:translateY(-80px) rotateX(360deg);  
					-ms-transform:translateY(-80px) rotateX(360deg); 
					transform:translateY(-80PX) rotateX(360deg);
					box-shadow: 0 0 4px 3px rgba(0,0,0,.2);
				}
			}	
		}
		.text-container {	
			position: relative;
			font-size: 16px;	    		
		    margin-top: 5px;
		    padding: 10px 0;		    
		    display: inline-block;
		    vertical-align: middle;
		    text-align: center;
		    overflow: hidden;
		    -webkit-transition: all 0.5s;
		    -moz-transition: all 0.5s;
		    -o-transition: all 0.5s;
		    transition: all 0.5s;
		    span {
		    	padding: 10px;
		    	color: $brand-primary;
		    	background: $light-color;
		    }
		    &:hover {
		    	span {
		    		background: $brand-primary !important;
		    		color: $light-color !important;
		    		-webkit-transition: all 0.5s;
				    -moz-transition: all 0.5s;
				    -o-transition: all 0.5s;
				    transition: all 0.5s;
		    	}
		    }
		}			
	}
}


