.ishislider {
	@media (max-width: 544px) {/* If media is below 544 */
		margin-bottom: 40px;
	}
	margin-bottom:30px;
	overflow:hidden;
	.owl-nav{
		margin-bottom: 0;
		position: unset;
		.owl-prev, .owl-next{
			@media (max-width: 991px) {/* If media is below 992 */
				top: 44%;
			}
			@media (max-width: 767px) {/* If media is below 768 */
				display: none;
			}
			border: 0;
			background-color: $brand-secondary !important;
			border-radius: 50%;
			border: 0 !important;
			position: absolute;
			padding: 10px;
	    	top: 46%;
			margin:4px;
			left:70px;
			right:auto;
			width: 40px;
			height: 40px;
			overflow: hidden;
			opacity: 0;
		    -webkit-transition: all 0.3s ease-in-out;
			-moz-transition: all 0.3s ease-in-out;
			-o-transition: all 0.3s ease-in-out;
			transition: all 0.3s ease-in-out;
			&:hover{
				background-color: $brand-third !important;	
				i {
					&:before {
						color: $light-color !important;
					}
				}			
			}
			i {				
				&:before {
					font-size: 16px;
					left: 0;
					right: 0;
					color: $brand-primary;
					position: absolute;
					top: 0;
					font-weight: 600;	
					text-align: center;		
					line-height: 39px;	
				}
			}
		}
		.owl-next{
			right: 70px;
			left: auto;
		}
	}
	.owl-dots {
		position: absolute;
		bottom: 10px;
		right: 0;
		left: 0;
		margin: 0 auto;
		text-align: center;
	    -webkit-transition: all 0.3s;
		-moz-transition: all 0.3s;
		-o-transition: all 0.3s;
		transition: all 0.3s; 
		.owl-dot {
			display: inline-block;
			vertical-align: middle;
			outline: none;
			span {
				width: 12px;
				height: 12px;
				border: 0;
				background: $brand-primary;
				border-radius: 50%;
				margin:0 3px;
				display: block;
			}
			&.active {
				span {
					background : $brand-secondary;	
				}
			}
		}
	}
	&:hover {
		.owl-nav{
			.owl-prev, .owl-next{
		    	opacity: 1;
			}
			.owl-prev {
				left: 50px;
			}
			.owl-next{
		    	right: 50px;
			}
		}
	}
}


