.ishibannerblock{
	text-align: center;
	.image-container{
		img{
			max-width: 100%;
			width: 100%;
			-webkit-transition: all 0.5s;
            -moz-transition: all 0.5s;
            -o-transition: all 0.5s;
            transition: all 0.5s;			
		}
		a{
			display: block;
			position: relative;
			overflow: hidden;
			-webkit-transition: all 0.5s;
		    -moz-transition: all 0.5s;
		    -o-transition: all 0.5s;
		    transition: all 0.5s;
			&:before, &:after {
				content: "";
				background: none repeat scroll 0 0 rgba(0, 0, 0, 0.1);
				bottom: -100%;
				width: 100%;
				height: 100%;
				display: block;
				left: 0;
				opacity: 0;
				position: absolute;
				-webkit-transition: all 0.8s ease 0s;
				-moz-transition: all 0.8s ease 0s;
				-ms-transition: all 0.8s ease 0s;
				-o-transition: all 0.8s ease 0s;
				transition: all 0.8s ease 0s;
			}
			&:after {		
				top: -100%;
				bottom: auto;
			}
		}
		&:hover {
			a {
				&:before {
					bottom: 50%;
					opacity: 1;
				}
				&:after {
					top: 50%;
					opacity: 1;
				}
			}
		}
	}
	.bannerblock{
		display: inline-block;
		vertical-align: middle;
		margin-bottom: 30px;
		@media (max-width: 767px) {/* If media is below 768 */
			margin-bottom: 15px;
		}
		&:last-child {
			margin-bottom: 0;
		}
	}
	.bannerblock3 {
		@media (max-width: 767px) {/* If media is below 768 */
			margin-bottom: 15px;
		}
		margin-bottom: 0;
	}
	.bannerblock1 {
		@media (max-width: 435px) {/* If media is below 435 */
			width:100%;
		}
	}
	.bannerblock2 {
		@media (max-width: 435px) {/* If media is below 435 */
			display: none;
		}
	}

	#ishispecialdeals{
		position: relative;
		margin-bottom: 30px; 
		@media (max-width: 767px) {/* If media is below 768 */
			margin-bottom: 15px;
		}
		.countdown-container{
			@media (max-width: 1199px) {/* If media is below 1200 */
				padding: 10px 0;
			}
			margin-bottom: 0;
			position: relative;
			background: $light-color;
			border: 1px solid $border-color;
			border-radius: 0;
			padding: 12px;
			.countdown-text{
				font-weight: 400;
				margin-bottom: 15px;
				color: $brand-primary;
				display: none;
			}
			.counter{
				display: inline-block;
				vertical-align: middle;
				text-align: center;
				font-weight: 400;
				cursor:pointer;
				position: relative;
				span{
					display: block;
				}
				&.countdown-seconds {
					.data {
						color: $brand-third;
						&:hover{
							color: $brand-primary;
						}
					}
				}
				.data{	
					@media screen and (min-width: 992px) and (max-width: 1199px) {/* If media is between 992 and 1199 */
						font-size: 15px;
						padding: 0 12px;
					}	
					@media (max-width: 543px) {/* If media is below 544 */
						font-size: 17px;
						padding: 0 12px;
					}
					font-size: 20px;
					color: $black-color;
					position: relative;
					padding: 0 15px;
					display: inline-block;
					vertical-align: middle;
					&:hover{
						color: $brand-third;
					}
					&:after {
						content: ':';
						position: absolute;
						right: -4px;
						top: -1px;
						font-size: 20px;
						color: $brand-third;
					}
				}
				.lbl{
					@media (min-width: 1200px) {/* If media is above 1199 */
						font-size: 12px;
					}
					display: none;
					font-size: 13px;
					font-weight: 400;
					padding: 4px 0;
					color: $brand-primary;
					text-transform: uppercase;
					font-family: $primary-font;
				}
				&:last-child{
					.data{
						&:before, &:after{
							display: none;
						}
					}
				}
			}
		}
		.product-thumb {
			padding: 0;
		}
		.caption{
			text-align: center;
			position: relative;
			padding-bottom: 0;
			h4{
				margin-bottom: 8px;
				a{
					@media (min-width: 1200px) {/* If media is above 1199 */
						font-size: 13px;
					}
					font-weight: 400;
				}
			}
			.price{
				margin-bottom: 8px;
				span{
					font-size: 15px;
				}
			}			
		}
		.owl-nav {
			@media (max-width: 543px) {/* If media is below 544 */
				margin-top: 15px;
			}
			@media (min-width: 544px) {/* If media is above 543 */
				.owl-prev, .owl-next {
					left: -18px;
					top: 43%;
				}
				.owl-next {
					right: -18px;
					left: auto;
				}
				opacity: 0;
			}
		}
		&:hover {
		    .owl-nav {
		      @media (min-width: 544px) {/* If media is above 543 */
		          opacity: 1;
		      }
		    }
		}
	}	
}


